.marquee-wrapper {
  text-align: center;
}

.marquee-wrapper .container {
  overflow: hidden;
  max-width: 100%;
}

.marquee-inner span {
  float: left;
  width: 50%;
}

.marquee-wrapper .marquee-block {
  --total-marquee-items: 11;
  height: 204px;
  width: calc(177.5px * (var(--total-marquee-items)));
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
  margin: 10px auto;
}

.marquee-inner {
  display: -webkit-block;
  display: -moz-block;
  display: -ms-block;
  display: block;
  width: 200%;
  position: absolute;
}

.marquee-inner.to-left {
  -webkit-animation: marqueeLeft 60s linear infinite;
  animation: marqueeLeft 60s linear infinite;
}

.marquee-inner.to-right {
  -webkit-animation: marqueeRight 60s linear infinite;
  animation: marqueeRight 60s linear infinite;
}

#aboutme p {
  font-size: 22px;
  color: white;
  padding: 40px;
  text-align: justify;
  max-width: 90vw;
  margin: auto;
}

.marquee-item {
  width: 180px;
  height: auto;
  display: -ms-inline-flexbox;
  display: inline-block;
  margin: 0 5px;
  /* float: left; */
  transition: all 0.2s ease-out;
  border: 2px solid #343337;
  padding: 8px;
}

@-webkit-keyframes marqueeLeft {
  0% {
    -webkit-left: 0;
    -moz-left: 0;
    -o-left: 0;
    -ms-left: 0;
    left: 0;
  }

  100% {
    -webkit-left: -100%;
    -moz-left: -100%;
    -o-left: -100%;
    -ms-left: -100%;
    left: -100%;
  }
}

@keyframes marqueeLeft {
  0% {
    -webkit-left: 0;
    -moz-left: 0;
    -o-left: 0;
    -ms-left: 0;
    left: 0;
  }

  100% {
    -webkit-left: -100%;
    -moz-left: -100%;
    -o-left: -100%;
    -ms-left: -100%;
    left: -100%;
  }
}

@-webkit-keyframes marqueeRight {
  0% {
    -webkit-left: -100%;
    -moz-left: -100%;
    -o-left: -100%;
    -ms-left: -100%;
    left: -100%;
  }

  100% {
    -webkit-left: 0;
    -moz-left: 0;
    -o-left: 0;
    -ms-left: 0;
    left: 0;
  }
}

@keyframes marqueeRight {
  0% {
    -webkit-left: -100%;
    -moz-left: -100%;
    -o-left: -100%;
    -ms-left: -100%;
    left: -100%;
  }

  100% {
    -webkit-left: 0;
